import React from 'react';

function Introduction() {
  return (
    <div className="introduction-section">
      <h1 className="introduction-section__title">Rafael Lucas</h1>
      <div className="container">
        <h2>Hi! My name is <span className="no-wrap">Rafael Lucas</span>,<br /> Im a designer specializing <br /> in  interactive experiences<br /> living in Braslia, Brazil.</h2>
      </div>
    </div>
  );
}

export default Introduction;



